import React from 'react';
import * as d3 from 'd3';
import { data } from '../../res/basicBandChartData'

// const parseDate = d3.timeParse("%Y%m%d")//
const convertData = data.map(d => {
    return { date: new Date((d.date)), high: +d.high, low: +d.low }
})

export default class extends React.Component {

    componentDidMount() {
        this._setupD3();
    }

    componentDidUpdate() {
        this._setupD3();
    }

    render() {
        return (
            <div id="graph" />
        )
    }

    _setupD3 = () => {

        //
        let width = document.getElementById("graph").clientWidth, height = 500,
            margin = ({ top: 20, right: 0, bottom: 30, left: 40 }),
            //x
            x = d3.scaleTime()//-
                .domain(d3.extent(convertData, d => d.date))//d3.extent - .
                .range([margin.left, width - margin.right]),
            //y    
            y = d3.scaleLinear()//
                .domain([d3.min(convertData, d => d.low), d3.max(convertData, d => d.high)])
                .nice(5)// domain ;                
                .range([height - margin.bottom, margin.top]),
            //x
            xAxis = g => g
                .attr("transform", `translate(0,${height - margin.bottom})`)//
                //axisBottom:  ; ticks :  tickSizeOuter().
                .call(d3.axisBottom(x).ticks(width / 80).tickSizeOuter(0))
                .call(g => g.select(".domain").remove()),// 
            //y
            yAxis = g => g
                .attr("transform", `translate(${margin.left},0)`)//
                .call(d3.axisLeft(y))
                .call(g => g.select(".domain").remove()),// 

            //.
            area = d3.area()
                .curve(d3.curveStep)//.
                .x(d => x(d.date))//  x0  x1 .
                .y0(d => y(d.low))//  y .
                .y1(d => y(d.high))// y .

            

        d3.select("#graph").select("svg").remove();
        let svg = d3.select("#graph").append("svg")
            .attr("width", width)//
            .attr("height", height);//

        svg.append("path")
            .datum(convertData)
            .attr("fill", "steelblue")
            .attr("d", area);

        svg.append("g")
            .call(xAxis);

        svg.append("g")
            .call(yAxis);


    }
}
