import React from 'react';
import * as d3 from 'd3';

export default class QuickStart extends React.Component {
    componentDidMount() {
        this._setupD3();
    }
    render() {
        return (
            //1.  ; 
            <div id="graph" />
        )
    }

    _setupD3 = () => {
        //2. 
        //D3  SVG  D3  SVG  SVG  SVG 
        /*
        SVGScalable Vector Graphics
        
        SVG  XML  SVG  HTML 
        
        SVG 

        SVG 
         XML JavaScript 
        
        
        */
        let svg = d3.select("#graph")
            .append("svg")//
            .attr("width", 500)//
            .attr("height", 500);//

        //3. 
        //  
        let linear = d3.scaleLinear()//
            .domain([0, 2500])// 0-2500  0-100
            .range([0, 250]);//0 2500    

        //4.  
        //  data 
        svg.selectAll("rect")   //svg
            .data(this.props.data)  //
            .enter()        //enter
            .append("rect") // rect
            //
            .attr("x", 25) //x
            .attr("y", (d, i) => i * 40) //y
            //width
            .attr("width", (d, i) => {
                // console.log("{d,i}", { d, i })
                // console.log("linear(d)", linear(d))
                return linear(d);
            })
            .attr("height", 25)
            //
            .attr("fill", "#66ccff");

        //5. 
        let g = svg.append("g")
        //x
        g.append("g")// SVG  <g> <g> 
            .attr("transform", "translate(25,0)")// transform 
            .call(
                d3.axisBottom(linear)//axisBottom
                .ticks(5)//
            )
        //y
        // 	svg.append("g").call(axis);   axis(svg.append(g));
        g.append("g")
            .attr("transform", "translate(25,0)")
            .call(d3.axisLeft(linear).ticks(0))



        // react/g x/y/width/height/fill  svg
        // MDN  :
        // [SVG](https://developer.mozilla.org/zh-CN/docs/Web/SVG/Element)
        // [SVG](https://developer.mozilla.org/zh-CN/docs/Web/SVG/Attribute)
        // [SVG](https://developer.mozilla.org/zh-CN/docs/Web/SVG/Tutorial)        
    }
    static defaultProps = {
        data: [2500, 2100, 1700, 1300, 900],
    }
}