import React from 'react';
import * as d3 from 'd3';
import FetchDataPromise from '../../res/basicMutiLineChartData'

export default class extends React.Component {

    componentDidMount() {
        this._setup();
    }

    componentDidUpdate() {
        this._setup();
    }

    render() {
        return (
            <div id="graph" />
        )
    }

    _setup = () => {
        FetchDataPromise().then(data => this._setupD3(data))
    }

    _setupD3 = (data) => {

        //
        let width = document.getElementById("graph").clientWidth, height = 500,
            margin = ({ top: 20, right: 0, bottom: 30, left: 40 }),
            //x
            x = d3.scaleTime()//-
                .domain(d3.extent(data.dates))//d3.extent - .
                .range([margin.left, width - margin.right]),
            //y    
            y = d3.scaleLinear()//
                .domain([0, d3.max(data.series, d => d3.max(d.values))])
                .nice()// domain ;                
                .range([height - margin.bottom, margin.top]),
            //x
            xAxis = g => g
                .attr("transform", `translate(0,${height - margin.bottom})`)//
                //axisBottom:  ; ticks :  tickSizeOuter().
                .call(d3.axisBottom(x).ticks(width / 80).tickSizeOuter(0)),

            //y
            yAxis = g => g
                .attr("transform", `translate(${margin.left},0)`)//
                .call(d3.axisLeft(y))
                .call(g => g.select(".domain").remove()),// 

            //.
            line = d3.line()
                .defined(d => !isNaN(d))
                .x((d, i) => x(data.dates[i]))
                .y(d => y(d))

        d3.select("#graph").select("svg").remove();
        let svg = d3.select("#graph").append("svg")
            .attr("width", width)//
            .attr("height", height);//

            //svgx y 
        svg.append("g").call(xAxis);
        svg.append("g").call(yAxis);

        //
        const path = svg.append("g")
            .attr("fill", "none")
            .attr("stroke", "steelblue")
            .attr("stroke-width", 1.5)
            .attr("stroke-linejoin", "round")
            .attr("stroke-linecap", "round")
            .selectAll("path")
            .data(data.series)
            .enter().append("path")
            .style("mix-blend-mode", "multiply")
            .attr("d", d => line(d.values));

        //
        this.hover(svg,path,x,y,data);
    }

    hover = (svg, path, x, y, data) => {
        svg.style("position", "relative");

        //.
        if ("ontouchstart" in document)
            svg.style("-webkit-tap-highlight-color", "transparent")
                .on("touchmove", moved)
                .on("touchstart", entered)
                .on("touchend", left)
        else
            svg.on("mousemove", moved)
                .on("mouseenter", entered)
                .on("mouseleave", left);

        const dot = svg.append("g")
            .attr("display", "none");

        dot.append("circle")
            .attr("r", 2.5);

        dot.append("text")
            .style("font", "10px sans-serif")
            .attr("text-anchor", "middle")
            .attr("y", -8);

        function moved() {
            d3.event.preventDefault();
            const ym = y.invert(d3.event.layerY);//invert - .
            const xm = x.invert(d3.event.layerX);
            const i1 = d3.bisectLeft(data.dates, xm, 1);//bisectLeft - 
            const i0 = i1 - 1;
            const i = xm - data.dates[i0] > data.dates[i1] - xm ? i1 : i0;
            //
            const s = data.series.reduce((a, b) => Math.abs(a.values[i] - ym) < Math.abs(b.values[i] - ym) ? a : b);            
            //stroke
            //filter - 
            //raise - 
            //  #ddd
            path.attr("stroke", d => d === s ? null : "#ddd").filter(d => d === s).raise();
            //dot 
            dot.attr("transform", `translate(${x(data.dates[i])},${y(s.values[i])})`);
            dot.select("text").text(s.name);
        }

        function entered() {
            //svg 
            path.style("mix-blend-mode", null).attr("stroke", "#ddd");
            dot.attr("display", null);
        }

        function left() {
            //svg 
            path.style("mix-blend-mode", "multiply").attr("stroke", null);
            dot.attr("display", "none");
        }
    }
}
