import React from 'react';
import * as d3 from 'd3';
const data = require('../../res/basicStackedAreaChartData.json')

const convertData = data.map(d => {
    let values = d.values.map(v => +v)
    return { date: new Date(d.date), value: +d.value, name: d.name, values: values }
})
const color = d3.scaleOrdinal(d3.schemeCategory10).domain(convertData.map(d => d.name))

export default class extends React.Component {

    componentDidMount() {
        this._setupD3();
    }

    componentDidUpdate() {
        this._setupD3();
    }

    render() {
        return (
            <div id="graph" />
        )
    }

    _setupD3 = () => {

        //
        let width = document.getElementById("graph").clientWidth, height = 500,
            margin = ({ top: 20, right: 40, bottom: 30, left: 40 }),
            //x
            x = d3.scaleTime()//-
                .domain(d3.extent(convertData, d => d.date))//d3.extent - .
                .range([margin.left, width - margin.right]),
            //y    
            y = d3.scaleLinear()//
                .domain([0, d3.max(convertData, d => d.values[1])])
                .nice()// domain ;
                .range([height - margin.bottom, margin.top]),
            //x
            xAxis = g => g
                .attr("transform", `translate(0,${height - margin.bottom})`)//
                //axisBottom:  ; ticks :  tickSizeOuter().
                .call(d3.axisBottom(x).ticks(width / 80).tickSizeOuter(0)),
            //y
            yAxis = g => g
                .attr("transform", `translate(${width - margin.right},0)`)//
                .call(d3.axisRight(y))
                .call(g => g.select(".domain").remove()),// 

            //
            area = d3.area()
                .curve(d3.curveStep)// .
                .x(d => x(d.date))// x0  x1 .
                .y0(d => y(d.values[0]))// y .
                .y1(d => y(d.values[1]))// y .

        d3.select("#graph").select("svg").remove();
        let svg = d3.select("#graph").append("svg")
            .attr("width", width)//
            .attr("height", height);//

        svg.append("g")
            .selectAll("path")
            .data([...this._multimap(convertData.map(d => [d.name, d]))])
            .enter().append("path")
            .attr("fill", ([name]) => color(name))
            .attr("d", ([, values]) => area(values))
            .append("title")
            .text(([name]) => name);

        svg.append("g")
            .call(xAxis);

        svg.append("g")
            .call(yAxis);

        svg.append("g")
            .attr("transform", `translate(${margin.left + 1},${margin.top})`)
            .call(this._legend);
    }

    _legend = svg => {
        const g = svg
            .attr("font-family", "sans-serif")
            .attr("font-size", 10)
            .selectAll("g")
            .data(color.domain().slice().reverse())
            .enter().append("g")
            .attr("transform", (d, i) => `translate(0,${i * 20})`);

        g.append("rect")
            .attr("width", 19)
            .attr("height", 19)
            .attr("fill", color);

        g.append("text")
            .attr("x", 24)
            .attr("y", 9.5)
            .attr("dy", "0.35em")
            .text(d => d);
    }

    _multimap = (entries, reducer = (p, v) => (p.push(v), p), initializer = () => []) => {
        const map = new Map();
        for (const [key, value] of entries) {
            map.set(key, reducer(map.has(key) ? map.get(key) : initializer(), value));
        }
        return map;
    }
}
